/*css document*/
body,h1,h2,h3,h4,h5,h6,p,ul,ol,dl,dd,td,input,select,option,textarea{
  margin:0;
  padding:0;
}
html{
  font-size: 26.67vw;
}
body{
  font-family:"\5FAE\8F6F\96C5\9ED1";
}
html,body,#app{
  height: 100%;
}
.flex_sec{
  display: -webkit-flex;
  -webkit-box-orient:vertical;
  -webkit-box-direction:normal;
      -ms-flex-direction:column;
          flex-direction:column;
  height:100%;
}
ul,ol{
  list-style:none;
}
a{
  text-decoration:none;
}
img{
  border:none;
  display: block;
}
section{
  -webkit-flex:1;
  overflow-y: auto;
  overflow-x: hidden;
}

header{
  width: 100%;
  height: .74rem;
}
.header_one{
  width: 100%;
  height: .44rem;
  display: -webkit-flex;
  -webkit-align-items:center;
  -webkit-justify-content:space-between;
}
.head_left{
  display:-webkit-flex;
}
.head_left img{
  width:.14rem;
  height:.16rem;
  margin-left:.13rem;
}
.head_left span:nth-of-type(1){
  font-size: 14px;
  color:#333333;
  margin-left:.05rem;
  margin-right:.1rem;
}
.head_left span:nth-of-type(2){
  font-size: 14px;
  color:#ff8700;
  font-weight: 900;
}
.head_right{
  width:.2rem;
  height:.2rem;
  margin-right:.1rem;
}
.header_two{
  display: -webkit-flex;
  background: #f0f0f0;
  height:.3rem;
  width:3.51rem;
  border-radius:.14rem;
  margin:0 auto;
  -webkit-align-items:center;
  -webkit-justify-content:space-between;
}
.two_left{
  display:-webkit-flex;
  -webkit-align-items:center;
}
.two_left img{
  width:.13rem;
  height:.13rem;
  margin-left:.12rem;
  margin-right:.06rem;
}
.two_left span{
  font-size:14px;
  color:#999999;
}
.two_right{
  width:.18rem;
  height:.17rem;
  margin-right:.12rem;
}
